// Modulos
@import 'variables'; 
@import 'normalize';
@import 'grid';
@import 'forms';
@import 'video-banner';

body{
    font-family: $font-body;
    font-size: 16px;
}

// Titles y Texts
.title-sm{
    font-size: 28px;
    font-weight: 700;
    line-height: 35px;
    color: $dark;
    margin: 0;
}

.title-xs{
    font{
        size: 16px;
        weight: 700;
    }
    line-height: 30px;
    margin: 0;
}


.text-normal{
    font-size: 16px;
    line-height: 27px;
    color: $gray-dark;
}
.uppercase{
    text-transform: uppercase;
}

// Colors Text y Background
.text-blue{
    color: $blue;
}

.bg-gray{
    background: $gray-light;
}

.section{
    padding: 2rem 0;
}

// padding
.p-5{
    padding: 10px;
}

.content-text{
    width: 500px;
}

@media screen and(max-width: 480px){
    .content-text{
        width: 100%;
    }
    .title-sm{
        text-align: center;
    }
    .title-xs{
        text-align: center;
    }
    .text-normal{
        text-align: justify;
    }
    .section{
        padding: 5rem 0;
    }
}
// Img Responsive
.responsive {
    width: 100%;
    height: auto;
}

.footer{
    background: #69758F;
    padding: 10px 0;
    .copy{
        margin-bottom: 0;
        color: #fff;
    }
}